@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&family=Raleway:ital,wght@1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&family=Raleway:ital,wght@1,100&family=Tektur:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.hero {
  position: relative;
  width: 100%;
  height: 100vh;
}

.hero__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.hero__text-content {
  position: absolute;
  right: 35%;
  color: white;
  font-size: 4rem;
  padding: 15px;
}

h1 {
  font-family: 'Tektur', cursive;
}
p {
  font-size: 1rem;
  font-family: 'Raleway', sans-serif;
}




.hero__menu {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: white;
  padding: 20px;
  transition: left 0.3s ease-in-out;
  font-size: 12px;
  color: black;
  text-align: center;
}

.menu-open {
  left: 0;
  z-index: 0;
}


.close-button {
  position: absolute;
  top: 0;
  right: 20px;
  font-size: 2rem;
  padding: 5px;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  color: black
}
.button--success {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
.company-info {
    font-size: 1rem;
}
.slider {
  display: flex;
  align-items: center;
  margin: 20px;

  opacity: 0;
  -webkit-transform: scale(85%);
          transform: scale(85%);
  transition: 2s;
}

.slider--zoom {
  opacity: 1;
  -webkit-transform: scale(100%);
          transform: scale(100%);
  transition: 2s;
}

.slider__image {
  width: 60%;
}

.slider__content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.slider__title {
  text-align: center;
}

@media screen and (max-width: 800px) {
  .slider {
    flex-direction: column;
    background-color: rgb(236, 236, 236);
  }

  .slider__image {
    width: 100%;
  }
}

.scrolledSection {
  scroll-margin-top: 300px;
}
.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    font-size: 18px;
    font-weight: bold;
    background-color: transparent;
    border: none;
    color: #000;
    cursor: pointer;
}
.nav-link {
    color: black;
}
.number {
    font-size: 25px;
    color: brown;
    text-align: end;
}



a { color: green!important;}
.contact-form {
    max-width: 80vw;
    margin: 0 auto;
    padding: 20px;
}
.contact-form__info {
    text-align: center;
}
.contact-form__title {
    font-size: 24px;
    margin-bottom: 10px;
}

.contact-form__description {
    font-size: 26px;
    margin-bottom: 20px;
    color: #565e64;
}

.contact-form__label {
    display: block;
    margin-bottom: 5px;
}

.contact-form__input,
.contact-form__textarea {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 20px;
}

.contact-form__input:focus,
.contact-form__textarea:focus {
    outline: none;
    border: 1px solid #0077cc;
}

.contact-form__button {
    background-color: #0077cc;
    color: #fff;
    padding: 10px 16px;
    font-size: 18px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.contact-form__button:hover {
    background-color: #005fa3;
}

@media screen and (max-width: 768px) {
    .contact-form {
        max-width: 100%;
        padding: 10px;
    }

    .contact-form__title {
        font-size: 20px;
        margin-bottom: 5px;
    }

    .contact-form__description {
        font-size: 14px;
        margin-bottom: 10px;
    }

    .contact-form__input,
    .contact-form__textarea {
        padding: 6px;
        font-size: 14px;
        margin-bottom: 10px;
    }

    .contact-form__button {
        padding: 8px 12px;
        font-size: 16px;
    }
}
